.banner{
    background-image: url("../../Images/brand.jfif");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: white;
}
.banner>h1{
    margin: 5vmax;

    font:600 2.5rem "Times New Roman"
}
.banner>p{
    font:300 1.5rem "Lucida sans"
}
.banner > a >button{
    margin-bottom: 5vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white;
    border-radius: 0;
    padding: 1vmax;
    transition: all 0.5s;
    width: 9vmax;
    font:500 1rem "Roboto"
}
.banner > a >button:hover{
    background-color: transparent;
    color: white;
}

.banner::after{
    content: "";
    width: 100vw;
    height: 100vh;
    background-color: white;
    position: absolute;
    top: 0%;
    left: 0%;
    clip-path: polygon(100% 77%, 0 100%, 100% 100%);
    max-width: 100%;
}
.homeHeading{
    text-align: center;
    font-family: "Roboto";
    font-size: 1.4rem;
    border-bottom: 1px solid rgb(21, 21, 21, 0.5);
    width: 30%;
    padding: 1vmax;
    margin: 5vmax auto;
}
.container{
    display:flex;
    margin: 2vmax auto;
    width: 80vw;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 100%;
}
.productCard{
    width:14vmax;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: rgb(48, 48, 48);
    margin: 2vmax;
    transition:ease-in 0.5s;
    padding-bottom: 0.5vmax;
}
.productCard > img{
    width: 14vmax;
}
.productCard > div {
    margin: 0.5vmax;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.productCardSpan{
    margin: 0.5vmax ;
    font:300 0.7vmax "Roboto";
}
.productCard > p{
    font-size: 1.5vmax;
    font-family: "Roboto";
    margin: 1vmax 0.5vmax;
    margin-bottom: 0;
}
.productCard > span{
    margin: 0.5vmax 0.5vmax;
    color: tomato;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1vmax;
}
.productCard:hover{
    box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);

    transform: translateY(-1vmax) ;
}




@media screen and (max-width:600px){
    .productCard>p{
        font-size: 1.7vmax;
    }
    .productCard>div{
        margin: 0.5vmax;
        display: block;
    }
    .productCard>span{
        font-size: 1.5vmax;
    }
    .productCard>div>span{
        margin: 0 0.5vmax;
        font:300 1.5vmax "Roboto";
    }

}